import React from "react";
import "./Home.scss";
import useHomeText from "./homeHooks/useHomeText";
// import Resume from "../../assets/Resume/resume.txt";

const text_attributes = ["Software Engineer.", "Creative Problem Solver."];

export default function Home() {
  // Custom Hook
  const typedHomeText = useHomeText(text_attributes);

  return (
    <div id="home" className="home-wrapper">
      <div className="home-section">
        <div className="banner-image vh-100 d-flex justify-content-center align-items-center">
          <div className="layer">
            <div className="home-container container-md">
              <div className="row justify-content-start">
                <div className="home-brand">
                  <div className="home-intro">
                    <span><span className="home-intro-space"/>HELLO WORLD</span>
                  </div>
                  <div className="home-text">
                    <span>
                      I am Mason Watson, <br />
                    </span>
                    <span className="blinking-cursor">a {typedHomeText}</span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
