import './App.css';
import Header from './PortfolioContainer/Header/Header';
import VerticalNavbar from './PortfolioContainer/VerticalNavbar/VerticalNavbar';
import Home from './PortfolioContainer/Home/Home';
import Contact from './PortfolioContainer/Contact/Contact';
import About from './PortfolioContainer/About/About';
import Timeline from './PortfolioContainer/Timeline/Timeline';
import Projects from './PortfolioContainer/Projects/Projects';
import ScrollButton from './PortfolioContainer/ScrollButton/ScrollButton';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';

function App() {
  return (
    <Router basename='/'>
      <div className="App">
        <Header/>
        <Routes>
          <Route path="/" element={<><Home/><VerticalNavbar/></>}></Route>
          <Route path="/about" element={<><About/><VerticalNavbar/></>}></Route>
          <Route path="/timeline" element={<><Timeline/><VerticalNavbar/><ScrollButton/></>}></Route>
          <Route path="/projects" element={<><Projects/><VerticalNavbar/><ScrollButton/></>}></Route>
          <Route path="/contact" element={<Contact/>}></Route>
        </Routes>
      </div>
    </Router>
  );
}

export default App;