/* global $ */
import React from "react";
import "./Projects.css";
import sortingvis from "../../assets/Project/sorting-visualizer.JPG";
import matchingdice from "../../assets/Project/matching-dice.JPG";
import checkerspy from "../../assets/Project/checker-pygame.JPG";
import pathfindimg from "../../assets/Project/pathfinding-visualizer.JPG";

export default function Projects() {
  return (
    <div id="project" className="project-wrapper">
      <div className="project__wrapper">
      <div className="container">
          <div className="row g-4">
            <div className="col-12 col-md-6 col-lg-4">
              <div className="card">
                <img
                  src={pathfindimg}
                  alt="null"
                  className="card-img-top"
                ></img>
                <div className="card-body">
                  <h5 className="card-title">
                    Dijkstra's Algorithm Visualization Tool
                  </h5>
                  <p className="card-text">
                    This project utilizes Dijkstra's Algorithm and a min heap
                    data structure to find the shortest path between two nodes.
                  </p>
                  <div className="card-button-container">
                    <a
                      href="https://masonwatson.github.io/pathfinding-visualizer/"
                      target="_blank"
                      class="btn btn-primary card-button"
                    >
                      Visit the Application
                    </a>
                    <a
                      href="https://github.com/masonwatson/pathfinding-visualizer/tree/master"
                      target="_blank"
                      class="btn btn-primary card-button"
                    >
                      View the Repository
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12 col-md-6 col-lg-4">
              <div className="card">
                <img src={sortingvis} alt="null" className="card-img-top"></img>
                <div className="card-body">
                  <h5 className="card-title">
                    Sorting Algorithm Visualization Tool
                  </h5>
                  <p className="card-text">
                    This project utilizes various sorting algorithms, such as
                    merge and bubble sort, to sort an array of random data from
                    smallest to largest.
                  </p>
                  <div className="card-button-container">
                    <a href="https://masonwatson.github.io/sorting-visualizer/" target="_blank" class="btn btn-primary card-button">
                      Visit the Application
                    </a>
                    <a
                      href="https://github.com/masonwatson/sorting-visualizer/tree/master"
                      target="_blank"
                      class="btn btn-primary card-button"
                    >
                      View the Repository
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12 col-md-6 col-lg-4">
              <div className="card">
                <img src={checkerspy} alt="null" className="card-img-top"></img>
                <div className="card-body">
                  <h5 className="card-title">Checkers PyGame</h5>
                  <p className="card-text">
                    This project uses Python and the PyGame library to implement
                    the classic game of checkers.
                  </p>
                  <div className="card-button-container">
                    {/* <a href="#" class="btn btn-primary card-button">
                      Watch the Demo
                    </a> */}
                    <a href="https://github.com/masonwatson/checkers-pygame" target="_blank" class="btn btn-primary card-button">
                      View the Repository
                    </a>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12 col-md-6 col-lg-4">
              <div className="card">
                <img src={matchingdice} alt="null" className="card-img-top"></img>
                <div className="card-body">
                  <h5 className="card-title">
                    Matching Dice Game
                  </h5>
                  <p className="card-text">
                    An automated matching dice game written in C. This project
                    uses POSIX threads to determine player order. The winning
                    team is determined when both players on a team roll the same
                    total.
                  </p>
                  <div className="card-button-container">
                    {/* <a href="#" class="btn btn-primary card-button">
                      Watch the Demo
                    </a> */}
                    <a href="https://github.com/masonwatson/matching-dice-game" target="_blank" class="btn btn-primary card-button">
                      View the Repository
                    </a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
