import React, { useState, useEffect } from "react";

const Phase = {
  Typing: 0,
  Pausing: 1,
  Deleting: 2,
};

const TYPING_INTERVAL = 85;
const PAUSE_TIME = 3000;
const DELETING_INTERVAL = 75;

export default function useHomeText(text_attributes) {
  const [selectedIndex, setSelectedIndex] = useState(0);
  const [phase, setPhase] = useState(Phase.Typing);
  const [typedHomeText, setHomeText] = useState("");

  // Home Text - typewriter effect
  useEffect(() => {
    switch (phase) {
      case Phase.Typing: {
        const nextHomeText = text_attributes[selectedIndex].slice(
          0,
          typedHomeText.length + 1
        );

        if (nextHomeText === typedHomeText) {
          setPhase(Phase.Pausing);
          return;
        }

        const timeout = setTimeout(() => {
          setHomeText(nextHomeText);
        }, TYPING_INTERVAL);

        return () => clearTimeout(timeout);
      }

      case Phase.Deleting: {
        if (!typedHomeText) {
          const nextIndex = selectedIndex + 1;
          setSelectedIndex(text_attributes[nextIndex] ? nextIndex : 0);
          setPhase(Phase.Typing);
          return;
        }

        const nextRemaining = text_attributes[selectedIndex].slice(
          0,
          typedHomeText.length - 1
        );

        const timeout = setTimeout(() => {
          setHomeText(nextRemaining);
        }, DELETING_INTERVAL);

        return () => clearTimeout(timeout);
      }
      case Phase.Pausing:
      default:
        const timeout = setTimeout(() => {
          setPhase(Phase.Deleting);
        }, PAUSE_TIME);
        return;
    }

    if (phase === Phase.Pausing) return;
  }, [text_attributes, typedHomeText, selectedIndex, phase]);

  return typedHomeText;
}
