import React, { useState } from "react";
import "./ScrollButton.css";

const ScrollButton = () => {
  const [visible, setVisible] = useState(false);

  const toggleVisible = () => {
    const scrolled = document.documentElement.scrollTop;
    if (scrolled > 80) {
      setVisible(true);
    } else if (scrolled <= 80) {
      setVisible(false);
    }
  };

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };

  window.addEventListener("scroll", toggleVisible);

  return (
    <div className="sttButton">
      <button>
      <i
        className={"bi bi-arrow-up-square " + (visible ? "vfadein" : "vfadeout")}
        onClick={scrollToTop}
        style={{ display: visible ? "inline" : "none"}}
      />
    </button>
    </div>
  );
};

export default ScrollButton;
