import "./VerticalNavbar.scss";

export default function VerticalNav() {
  return (
    <div id="vn-wrapper" className="vnav-wrapper">
      <nav className="vnav__wrapper">
        <ul className="vnav">
          <li className="vnav-li">
            <a
              className="vnav-a"
              aria-current="page"
              href="https://www.linkedin.com/in/masonxw/"
              target="_blank"
              rel="noreferrer"
            >
              <span className="__link">
                LINKEDIN
              </span>
            </a>
          </li>
          <li className="vnav-li">
            <a
              className="vnav-a"
              aria-current="page"
              href="https://github.com/masonwatson"
              target="_blank"
              rel="noreferrer"
            >
              <span className="__link">
                GITHUB
              </span>
            </a>
          </li>
          <li className="vnav-li">
            <a
              className="vnav-a"
              aria-current="page"
              href="https://www.instagram.com/masonwatson.py/"
              target="_blank"
              rel="noreferrer"
            >
              <span className="__link">
                INSTAGRAM
              </span>
            </a>
          </li>
          <li className="vnav-li">
            <a
              className="vnav-a"
              aria-current="page"
              href="https://twitter.com/mxwcode"
              target="_blank"
              rel="noreferrer"
            >
              <span className="__link">
                TWITTER
              </span>
            </a>
          </li>
        </ul>
      </nav>
    </div>
  );
}