import React, { useState } from "react";
import "./Header.scss";
import { Link } from "react-router-dom";

export default function Header() {
  const [homeActive, setHomeActive] = useState(true);
  const [aboutActive, setAboutActive] = useState(false);
  const [timelineActive, setTimelineActive] = useState(false);
  const [contactActive, setContactActive] = useState(false);
  const [projectsActive, setProjectsActive] = useState(false);

  return (
    <nav
      id="navbar"
      // className={`navbar fixed-top bg-dark navbar-expand-lg navbar-dark p-md-3`}
      className={`navbar fixed-top bg-dark navbar-expand-lg navbar-dark p-md-3`}
    >
      <div className="container-fluid">
        <a className="navbar-brand" 
           href="https://masonxwatson.com/">
          Mason Watson
        </a>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#toggleMobileMenu"
          aria-controls="toggleMobileMenu"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <i class="bi bi-list"></i>
        </button>
        <div className="collapse navbar-collapse" id="toggleMobileMenu">
          <ul className="navbar-nav text-center me-auto mb-2 mb-lg-0">
            <Link className="link" to="/">
              <li className="nav-item">
                <a
                  className={`nav-link ${
                    homeActive ? "header-nav-active" : "header-nav-inactive"
                  }`}
                  aria-current="page"
                  href="#"
                  onClick={() => {
                    setHomeActive(true);
                    setAboutActive(false);
                    setTimelineActive(false);
                    setContactActive(false);
                    setProjectsActive(false);
                  }}
                >
                  Home
                </a>
              </li>
            </Link>
            <Link className="link" to="/about">
              <li className="nav-item">
                <a
                  className={`nav-link ${
                    aboutActive ? "header-nav-active" : "header-nav-inactive"
                  }`}
                  aria-current="page"
                  href="#"
                  onClick={() => {
                    setHomeActive(false);
                    setAboutActive(true);
                    setTimelineActive(false);
                    setContactActive(false);
                    setProjectsActive(false);
                  }}
                >
                  About
                </a>
              </li>
            </Link>
            <Link className="link" to="/timeline">
              <li className="nav-item">
                <a
                  className={`nav-link ${
                    timelineActive ? "header-nav-active" : "header-nav-inactive"
                  }`}
                  aria-current="page"
                  href="#"
                  onClick={() => {
                    setHomeActive(false);
                    setAboutActive(false);
                    setTimelineActive(true);
                    setContactActive(false);
                    setProjectsActive(false);
                  }}
                >
                  Timeline
                </a>
              </li>
            </Link>
            <Link className="link" to="/projects">
              <li className="nav-item">
                <a
                  className={`nav-link ${
                    projectsActive ? "header-nav-active" : "header-nav-inactive"
                  }`}
                  aria-current="page"
                  href="#"
                  onClick={() => {
                    setHomeActive(false);
                    setAboutActive(false);
                    setTimelineActive(false);
                    setContactActive(false);
                    setProjectsActive(true);
                  }}
                >
                  Projects
                </a>
              </li>
            </Link>
            <Link className="link" to="/contact">
              <li className="nav-item">
                <a
                  className={`nav-link ${
                    contactActive ? "header-nav-active" : "header-nav-inactive"
                  }`}
                  aria-current="page"
                  href="#"
                  onClick={() => {
                    setHomeActive(false);
                    setAboutActive(false);
                    setTimelineActive(false);
                    setContactActive(true);
                    setProjectsActive(false);
                  }}
                >
                  Contact
                </a>
              </li>
            </Link>
          </ul>
        </div>
      </div>
    </nav>
  );
}
