import React, { useState, useEffect } from "react";

const Phase = {
  Typing: 0,
  Pausing: 1,
  Deleting: 2,
};

const TYPING_INTERVAL = 75;
const PAUSE_TIME = 2000;
const DELETING_INTERVAL = 75;

export default function useAboutIntro(text_attributes) {
  const [selectedIndex, setSelectedIndex] = useState(0);
  const [phase, setPhase] = useState(Phase.Typing);
  const [typedAboutText, setAboutText] = useState("");

  // About Text - typewriter effect
  useEffect(() => {
    switch (phase) {
      case Phase.Typing: {
        const nextAboutText = text_attributes[selectedIndex].slice(
          0,
          typedAboutText.length + 1
        );

        if (nextAboutText === typedAboutText) {
          setPhase(Phase.Pausing);
          return;
        }

        const timeout = setTimeout(() => {
          setAboutText(nextAboutText);
        }, TYPING_INTERVAL);

        return () => clearTimeout(timeout);
      }

      case Phase.Deleting: {
        if (!typedAboutText) {
          const nextIndex = selectedIndex + 1;
          setSelectedIndex(text_attributes[nextIndex] ? nextIndex : 0);
          setPhase(Phase.Typing);
          return;
        }

        const nextRemaining = text_attributes[selectedIndex].slice(
          0,
          typedAboutText.length - 1
        );

        const timeout = setTimeout(() => {
          setAboutText(nextRemaining);
        }, DELETING_INTERVAL);

        return () => clearTimeout(timeout);
      }
      case Phase.Pausing:
      default:
        const timeout = setTimeout(() => {
          setPhase(Phase.Deleting);
        }, PAUSE_TIME);
        return;
    }

    if (phase === Phase.Pausing) return;
  }, [text_attributes, typedAboutText, selectedIndex, phase]);

  return typedAboutText;
}
